import { Injectable } from '@angular/core';
import { LoginData } from '../models/loginData';
import { Router } from '@angular/router';

@Injectable()
export class LoginService {
  isLogged = false;

  constructor(private router: Router) { }

  checkLogin(data: LoginData): Promise<string> {
    return new Promise(resolve => {
      if (data.passConfirm) {
        resolve(this.register(data));
      } else {
        resolve(this.login(data));
      }
    });
  }

  register(data: LoginData): string {
    if (data.pass === data.passConfirm) {
      localStorage.setItem('user', JSON.stringify({email: data.email, pass: data.pass}));
      this.isLogged = true;
      this.router.navigate(['/board']);
      return null;
    }
    return 'passwords doesn\'t match';
  }

  login(data: LoginData): string {
    const userData: LoginData = JSON.parse(localStorage.getItem('user'));
    if (userData && userData.email === data.email && userData.pass === data.pass) {
      this.isLogged = true;
      this.router.navigate(['/board']);
      return null;
    }
    return 'wrong email or password';
  }

  logOut(): void {
    this.isLogged = false;
    this.router.navigate(['/signin']);
  }

}



